 material for this, her use of neutral pronouns and lyrical style promotes it well. I was actually going to write about her album \"Dedicated\", with the song below lining up with the 'you is two' concept. This song would be the scene where the right brain, after trying to speak out through the use of music, after being just a witness for the most part, takes control and sings, but is also renouncing their temporary control of the 'words' in exchance for more control over the sound, or more control over HOW the individual goes about their life, while letting them choose the WHAT or figure out the WHY on their own later. I also hear snippets in all of Billie Eilish's music that fit, with self love at the core of her most recent album. The song \"my future\" is directly about singing a love song to her future self.\n\n      ")]),_vm._v(" "),_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com/embed/6o0EN7CoLao","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}}),_vm._v(" "),_c('p',[_vm._v("\n      At this time, I was listening to 'Hey I'm Just like You' on repeat often, and after I heard a lyric in this light, I couldn't not hear it. Maybe parts of this are obvious, but I argue that every character in this album is a different part of the same person. They are the performer and the audience, and that relationship evolves as they age. To demonstrate, I will break down the key lyrics from each song below. One important item to note first is the nature of this album. Tegan and Sara took material from their high school days and made music with the help of that. They wrote this album right as they were approaching the age of 40. Turning 30 soon myself, I vibe with the material. They are talking about how to grow with yourself, a midlife crisis of sorts, from the persective of a high schooler and a 40-year old simultaneously. Your relationship with yourself is the center of this album. The plot that ties the songs together is this self relationship evolving over time.\n      ")]),_vm._v(" "),_c('h5',[_vm._v("\n      Hold My Breath Until I Die\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics('Hold My Breath Until I Die')}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("\n      The core concept of this song confused me but it starts with brain concept (also found in The Fall) that the \"we\" we most associate with is a stream of consciousness. Not a single unbroken thought, but the cumulative thoughts that you pile up while awake. That is the immediate \"you\". This song is taking comfort in the temporary nature of feelings, that even if you feel bad now, another \"you\" will have a slightly different slate that they work with, and it isn't \"your\" problem now. If you hold your breath as long as you can, you pass out, lose consciousness, and other parts of your brain take over. When you \"wake up\", your brain has already been changed directly as it edits things during sleep. This starts our story by establishing that the MC is curious about how the brain works and is interested in manipulating it because of some level of struggle.\n    ")]),_vm._v(" "),_c('h5',[_vm._v("\n      Hey, I'm Just like You\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics("Hey, I'm Just like You")}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("\n      \"Stranded and I'm so lost, my mind is wired\nOvercome and overdone, my fate is on fire\nEat the drugs and dance to screaming in my head\nI'm so happy that we're friends, me and you\"\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      \"Together in the light, I swear all for you\nTonight in the dark, we're seeing colors\"\n    ")]),_vm._v(" "),_c('p',[_vm._v("\n      The first other 'self' we are introduced is the confident, drug-fueled self. T&S have talked before about their LSD use when they were young. A litte messed up and unsure of yourself, you lean on this version of yourself, it's \"fun you\". As the chorus emphasizes, there is a lot of power in this full embracement of self that the lifestyle enables. She feels empowered by this inner self she has found, and the relationship they've established.\n    ")]),_vm._v(" "),_c('h5',[_vm._v("\n      I’ll Be Back Someday\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics('I’ll Be Back Someday')}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("Now our main character is bored with their current/past self, and they are boldly going outward. They are not killing their past self necessarily, but they are ditching it in any direction, like an animal in a trap. they have found that they have been lying to themselves, or not living up to their own expectations and know that to get to where they want to be, they can't stay here. We hit a fun-damental problem with the brain and consciousness: it is fully shaped by the past, it wants to guess the future, and it only exists in the now. Knowing what she knows about  ")]),_vm._v(" "),_c('h5',[_vm._v("\n      Don’t Believe the Things They Tell You (They Lie)\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){_vm.showLyrics("Don’t Believe the Things They Tell You (They Lie)")}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("This song directly talks about a struggle with self love in the verses. The chorus discusses lying, pushing you to learn for yourself, question everything even if it is coming from yourself. The \"They\" in the title is both the voices of other people and the inner voices of the MC. In terms of brain science, thoughts and feelings aren't really directly controllable, only how we react to them. MC feels burned by their brain in its current state, and is trying to reevaluate her relationship with her brain. ")]),_vm._v(" "),_c('h5',[_vm._v("\n      Hello, I’m Right Here\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics("Hello, I’m Right Here")}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("This song goes back to the consciousness theme of the first song. 'Until I've done all that I can', that until could refer to until the day is over. \"Hello, I'm right here\" is the answer to that potential 'death', fully embracing the present")]),_vm._v(" "),_c('h5',[_vm._v("\n      I Don’t Owe You Anything\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics("I Don’t Owe You Anything")}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("The struggle is full-fledged, the MC has reverted back in their ways many times, but she have reached a limit. She trying to cast off her past self, still acknowledging how she wants to go back to that lifestyle. She is announcing her departure.")]),_vm._v(" "),_c('h5',[_vm._v("\n      I Know I’m Not the Only One\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics("I Know I’m Not the Only One")}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("This song is honoring the different 'selves', and moving on despite it. There is a duet here, maybe this is the most complicated song. ( they give it away with the 'yours' to 'my' words I think, follow up on it)")]),_vm._v(" "),_c('h5',[_vm._v("\n      Please Help Me\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics('Please Help Me')}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("In the struggle to be who they want to be, she is facing the fear of failure and not sure how to proceed. \"needed a plaace to rest my head\", she is embracing this current self that is giving her some ground to stand on, a foundation. \"who am I to disagree? who 