omeone else's head?'. 'All we get when we get together is bored'. Her relationship with herself is at its lowpoint. She is stuck, \"close my eyes and fall behind\", sleep is not a sufficient escape for her because her behaviors have her trapped. \"Have another drink it'll make you cool, it'll make you you\". It's a cycle she's grown acccustomted to, and she is not getting anything from it anymore.")]),_vm._v(" "),_c('h5',[_vm._v("\n      You Go Away and I Don’t Mind\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics('You Go Away and I Don’t Mind')}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("The stage of acceptance is here, she is ready to leave her past self behind. She still feels the same way, but wants to grow. \"it's your mind that matters most, it's your mind that makes you mine\" describes the relationship of RB on the nose, RB is singing to her here. As much as she wants to leave this part of herself in the past, she is now on better terms with the parts she is choosing to leave behind. ")]),_vm._v(" "),_c('h5',[_vm._v("\n      All I Have to Give the World Is Me\n    ")]),_vm._v(" "),_c('button',{attrs:{"type":"click"},on:{"click":function($event){return _vm.showLyrics('All I Have to Give the World Is Me')}}},[_vm._v("show lyrics")]),_vm._v(" "),_c('p',[_vm._v("\"Faced Myself in the Mirror Last night, I looked for things you said you liked\nThe person I am when no one's around,\nThe girl I don't want to be found\"")]),_vm._v(" "),_c('p',[_vm._v("(chorus)")]),_vm._v(" "),_c('p',[_vm._v("\n  \"All I have to give this world is me, and that's it\nAll I have to show this world is me, and that's it\nJust me, just me, just me\nAnd that's it\"\n")]),_vm._v("\n\"Convinced myself not to be so strange\nI hope I still take your breath away\nI look in the mirror, I see the face\nThe one who worries, it can change\nYou don't have to change, you don't have to change\nYou don't have to change\"\n\n"),_c('p',[_vm._v("\n  \"Staring down my two halves in the glass\nFeels like a heart attack\nTelling me to choose one or the other\nI get so panicked\nI don't wanna lose, but if I choose love\nWhat will happen then?\nStaring down my two halves in the glass\nWhat then?\nGo ahead and choose, go ahead and choose\nGo ahead and choose\"\n\n")]),_vm._v(" "),_c('p',[_vm._v("\n  That's kind of most of the song, but it all seems to finally be explicitly talking about her two inner selves, her inner (RB) self and the active consciousness that is her. The chorus brings home the idea of embracing yourself as you are, and being yourself unabashedly. She has finally realized that theres only one, herself, her divisions are all self-made. Her choice of who to be is moot because all she can do is be herself. In the process, she is learning how to love herself. The song ends with conflict, \"I don't wanna lose, but if I choose love, What will happen then? Staring down my two halves in the glass What then\". I think this conflict is saying, 'if I choose to love myself, how do I resolve what I am versus what I want to be?' But earlier she says \"Don't you ever wanna change?\", I think her choice to love herself is her motivation to want change, to move towards who she knows she is in comparison to who she sees in the mirror.\n")]),_vm._v(" "),_vm._m(1)]),_vm._v(" "),_c('div',{staticStyle:{"position":"sticky","top":"0","bottom":"0"}},[_c('Song',{attrs:{"lyrics":_vm.song}})],1)])}
